import { html, nothing } from 'lit-html'
import { iif, argPlaceholder } from '../libs/mobius-utils.js'
import { makeElementMaker, prefixClassWithMobius } from '../helpers/index.js'

const iifNothing = iif(argPlaceholder, argPlaceholder, nothing)

const makeTop = (top, classes) => iifNothing(top, html`
  <div class="mobius-display--flex mobius-flex-shrink--0 ${classes}">
    ${top}
  </div>
`)

const makeMiddle = (middle, classes) => iifNothing(middle, html`
  <div class="mobius-display--flex mobius-position--relative mobius-flex-grow--1 ${classes}">
    ${middle}
  </div>
`)

const makeBottom = (bottom, classes) => iifNothing(bottom, html`
  <div class="mobius-display--flex mobius-flex-shrink--0 ${classes}">
    ${bottom}
  </div>
`)

export const makeMiddleRowAdaptiveLayoutE = makeElementMaker({
  marks: {},
  styles: {
    container: '',
    'container-top': '',
    'container-middle': '',
    'container-bottom': ''
  },
  actuations: {},
  configs: {},
  handler: (view, { styles }) =>
    view`
      <div class="mobius-display--flex mobius-layout__vertical mobius-flex-wrap--nowrap ${'container'}">
        ${makeTop(styles.top, prefixClassWithMobius(styles['container-top']))}
        ${makeMiddle(styles.middle, prefixClassWithMobius(styles['container-middle']))}
        ${makeBottom(styles.bottom, prefixClassWithMobius(styles['container-bottom']))}
      </div>
    `
})
